import React, { useContext, useState } from "react"
import styled, { css } from "styled-components"
import { Padding, Text } from "../"
import { mapRoleToTextColor } from "../../theme"
import { Border, BorderStyle, Shadow, Spinner, InteractiveStyle } from "../basic"
import { Center, Overlay, Queue, Stack } from "../layout"
import { ValidatedFormContext } from "./form"
import _ from "lodash"
import { AnimatedToggleView } from "../animation"

export const DisabledStyleButton = css`
  opacity: ${({ disabled }) => (disabled ? "0.5" : 1)};
  pointer-events: ${({ disabled }) => (disabled ? "none" : "all")};
`

const RawButton = ({
  children,
  leftIcon,
  rightIcon,
  loading,
  className,
  radius = "small",
  role = "info",
  textRole,
  size,
  space,
  alignment,
  ...props
}) => {
  return (
    <Border
      {...props}
      className={className}
      role={role}
      radius={radius}
      borderSize={[1]}
    >
      <Center height="100%">
        <Padding size={size || [3, 6]}>
          <Queue size={space || 2}>
            {leftIcon}
            <Text type="primary5" role={textRole}>
              {children}
            </Text>
            {loading && (
              <Center>
                <Spinner />
              </Center>
            )}
            {!loading && <Center>{rightIcon}</Center>}
          </Queue>
        </Padding>
      </Center>
    </Border>
  )
}

export const Button = styled(RawButton)`
  display: inline-block;
  ${InteractiveStyle}
  ${DisabledStyleButton}
`

export const AsyncActionButton = ({ onClick, role, ...props }) => {
  let [loading, setLoading] = useState(false)
  let [error, setError] = useState()

  const click = async () => {
    setLoading(true)
    try {
      onClick && (await onClick())
    } catch (e) {
      setError(e.message)
      setTimeout(() => {
        setError(null)
      }, 4000)
    }
    setLoading(false)
  }
  return (
    <Border>
      <Button
        {...props}
        role={error ? "error" : role}
        loading={loading}
        onClick={click}
      />

      <AnimatedToggleView visible={!!error}>
        <Overlay bottom={-15}>
          <Text role="error" type="tertiary1">
            {error}
          </Text>
        </Overlay>
      </AnimatedToggleView>
    </Border>
  )
}

export const ValidatedFormButton = ({ onClick, ...props }) => {
  const { error, onSubmit } = useContext(ValidatedFormContext)
  const click = async () => {
    let error = await onSubmit()
    if (!_.isEmpty(error)) {
      return
    }
    onClick && onClick()
  }
  return (
    <AsyncActionButton
      {...props}
      onClick={click}
      disabled={!_.isEmpty(error)}
    />
  )
}

export const PrimaryButton = props => {
  const { children, active, size } = props
  return (
    <Button
      {...props}
      radius="xlarge"
      role="secondary"
      textRole={active ? "primary" : "secondary"}
      backgroundRole={active ? "secondary" : undefined}
      // space={active ? 0 : 5}
      size={size || (active ? [2, 7] : [2, 5])}
    >
      {children}
    </Button>
  )
}

export const AsyncPrimaryButton = props => {
  const { children, active, size, rightIcon } = props
  return (
    <AsyncActionButton
      {...props}
      radius="xlarge"
      role="secondary"
      textRole={active ? "primary" : "secondary"}
      backgroundRole={active ? "secondary" : undefined}
      rightIcon={active ? undefined : rightIcon}
      // space={active ? 0 : 5}
      size={size || (active ? [2, 7] : [2, 5])}
    >
      {children}
    </AsyncActionButton>
  )
}
