import React from "react"
import { Border, Shadow, Text } from "../basic"
import { Center, Overlay, Padding, Queue, Stack } from "../layout"
import { InteractiveStyle } from "../basic"
import { AnimatedToggleView } from "../animation"
import styled from "styled-components"
import { ReactNode } from "react"

export const Card = ({ children }) => {
  return (
    <Shadow width="100%">
      <Border radius="small" borderSize={[1, 1, 1, 1]} width="100%">
        <Padding size={5}>{children}</Padding>
      </Border>
    </Shadow>
  )
}

export const SelectableCardRaw = ({
  children,
  className,
  selected,
  radius = "medium",
  paddingSize = [2, 1],
  ...props
}) => {
  return (
    <Border
      {...props}
      className={className}
      role="primary"
      radius={radius}
      borderSize={[2]}
    >
      <AnimatedToggleView visible={selected}>
        <Overlay left={0} right={0} top={-3}>
          <Center>
            <Border
              backgroundRole="primary"
              className={className}
              role="primary"
              radius="small"
              borderSize={[1]}
            >
              <Padding size={paddingSize}>
                <Text role="secondary" type="primary6">
                  Selected
                </Text>
              </Padding>
            </Border>
          </Center>
        </Overlay>
      </AnimatedToggleView>
      <Padding size={4}>{children}</Padding>
    </Border>
  )
}

export const SelectableCard = styled(SelectableCardRaw)`
  ${InteractiveStyle}
`
