import styled, { css } from "styled-components"
import _ from "lodash"
import { mapRoleToBackgroundColor } from "../../theme"
import "./types"
export const fibonacci = _.memoize(function (n = 0) {
  return n < 2 ? n : fibonacci(n - 1) + fibonacci(n - 2)
})

export const Grid = styled.div`
  display: grid;
  align-items: ${({ align }) => align};
  grid-gap: ${({ theme, space = 3 }) => theme.baseSpace * space}px;
  grid-template-columns: ${({ theme, columns }) => {
    return _.isArray(columns)
      ? columns.map(d => `${d || 1}fr`).join(" ")
      : `repeat(auto-fill,minmax(${theme.baseSpace * fibonacci(9)}px, 1fr))`
  }};
  grid-auto-rows: 1fr;
`

export const dimensionToStyle = (size, baseSpace) => {
  if (_.isUndefined(size)) {
    return
  }
  return _.isString(size) ? size : `${fibonacci(size) * baseSpace}px`
}

export const DisabledStyle = css`
  opacity: ${({ disabled }) => (disabled ? "0.1" : 1)};
  pointer-events: ${({ disabled }) => (disabled ? "none" : "all")};
`
export const DimensionStyle = css`
  width: ${({ width, theme }) =>
    dimensionToStyle(width, theme.baseSpace)};
  height: ${({ height, theme }) =>
    dimensionToStyle(height, theme.baseSpace)};
  max-height: ${({ maxHeight }) => maxHeight};
  overflow-y: ${({ maxHeight }) =>
    maxHeight ? "scroll" : "visible"};
`

export const BackgroundStyle = css`
  background-color: ${({ backgroundRole }) =>
    mapRoleToBackgroundColor(backgroundRole)};
`

export const Container = styled.div`
  margin: 0 auto;
  max-width: ${({ theme }) => theme.desktop}px;
  position: relative;
`

export const Stack = styled.div`
  display: grid;
  grid-gap: ${({ theme, size }) => theme.baseSpace * fibonacci(size)}px;
  grid-template-columns: 1fr;
  position: relative;
  ${DimensionStyle}
  ${BackgroundStyle}
`
export const Queue = styled.div`
  display: flex;
  justify-content: ${({ justifyContent }) => justifyContent};
  justify-content: ${({ alignItems }) =>
    alignItems == "right" && "flex-end"};
  & > * + * {
    margin-left: ${({ theme, size }) =>
      theme.baseSpace * fibonacci(size)}px;
  }
  align-items: ${({ alignVertical }) => alignVertical || "center"};
  ${DimensionStyle}
  ${BackgroundStyle}
`
export const Margin = styled.div`
  margin: ${({ theme, size }) =>
    !_.isArray(size)
      ? `${fibonacci(size) * theme.baseSpace}px`
      : size.map(d => `${fibonacci(d) * theme.baseSpace}px`).join(" ")};
  ${BackgroundStyle}
  ${DimensionStyle}
`

export const Padding = styled.div`
  position: relative;
  padding: ${({ theme, size }) =>
    !_.isArray(size)
      ? `${fibonacci(size) * theme.baseSpace}px`
      : size.map(d => `${fibonacci(d) * theme.baseSpace}px`).join(" ")};
  ${DimensionStyle}
  ${BackgroundStyle}
`
const sideSpacing = key => ({ theme, ...props }) => {
  let side = _.property(key)(props)
  if (_.isUndefined(side)) {
    return
  }
  return dimensionToStyle(side, theme.baseSpace)
}
export const Overlay = styled.div`
  position: ${({ relative }) => (relative ? "relative" : "absolute")};
  z-index: ${({ zIndex }) => zIndex};
  top: ${sideSpacing("top")};
  right: ${sideSpacing("right")};
  left: ${sideSpacing("left")};
  bottom: ${sideSpacing("bottom")};
  display: flex;
  ${DimensionStyle}
  ${BackgroundStyle}
`

export const Center = styled.div`
  align-items: center;
  display: flex;
  justify-content: center;
  flex: 1;
  align-self: center;
  ${DimensionStyle}
  ${BackgroundStyle}
`
