import React, {useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import { BorderStyle } from '../basic/border';
import { TextStyle, Text, Spinner } from '../basic';
import {
    Stack,
    Overlay,
    Center,
    Padding,
    DimensionStyle,
    BackgroundStyle,
} from '../layout';
import { ValidatedFormContext } from './form';
import _ from 'lodash';

export const TextEntry = styled.input`
    outline: none;
    width: 100%;
    transition: all 0.2s;
    ${TextStyle}
    ${BorderStyle}
    ${DimensionStyle}
    ${BackgroundStyle}
`;

export const Input = ({
    label,
    placeholder,
    onChange,
    loading,
    error,
    rightIcon,
    width,
    labelType,
    ...props
}) => {
    let [active, setActive] = useState(false);

    return (
        <Stack size={2} width={width || '100%'}>
            <Text type={labelType || 'primary5'}>{label}</Text>
            {/* <Text type="primary6">{label}</Text> */}
            <Padding size={[0]}>
                <TextEntry
                    {...props}
                    width="100%"
                    height={6}
                    borderSize={[0, 0, 1]}
                    role={(error && 'error') || (active && 'primary') || 'info'}
                    onChange={({ target: { value } }) =>
                        onChange && onChange(value)
                    }
                    placeholder={placeholder}
                    onFocus={() => setActive(true)}
                    onBlur={() => setActive(false)}
                />

                {loading && (
                    <Overlay top={0} right={0} height="100%">
                        <Center>
                            <Spinner />
                        </Center>
                    </Overlay>
                )}
                {rightIcon && (
                    <Overlay top={0} right={0} height="100%">
                        <Center>{rightIcon}</Center>
                    </Overlay>
                )}
            </Padding>
            {error && (
                <Text role="error" type="tertiary1">
                    {error}
                </Text>
            )}
        </Stack>
    );
};

export const RequiredValidator = async (value) => {
    if (!value) {
        throw new Error('please enter this value');
    }
}




export const RegexValidator = (
    regex,
    message,
    allowEmpty = true
) => {
    return async (value) => {
        if (allowEmpty && _.isEmpty(value)) {
            return;
        }
        if (!regex.test(value || '')) {
            throw new Error(message);
        }
    };
};

export const EmailValidator = RegexValidator(
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@(([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3})|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
    'Please enter valid email'
);
export const NumberValidator = RegexValidator(
    /^\d+$/,
    'Please enter valid number'
);
export const ValidatedInput = ({
    validators = [],
    error: defaultError,
    onChange: onValueChange,
    value: defaultValue,
    ...props
}) => {
    const [error, setError] = useState();
    const [value, setValue] = useState();
    useEffect(() => setError(defaultError), [defaultError]);
    useEffect(() => setValue(defaultValue), [defaultValue]);

    const validate = async (value) => {
        let errorMessage = null;
        try {
            for (const validator of validators) {
                await validator(value);
            }
        } catch (validationError) {
            errorMessage = validationError.message;
        }
        setError(errorMessage);
        return errorMessage;
    };
    const onChange = async (value) => {
        onValueChange && onValueChange(value);

        await validate(value);
        setValue(value);
    };
    return <Input {...props} value={value} onChange={onChange} error={error} />;
};

export const ValidatedFormInput = (props) => {
    const { role, onChange, error: defaultError, value: defaultValue } = props;
    const {
        error,
        validators = [],
        onChange: onChangeValue,
        value,
    } = useContext(ValidatedFormContext);
    useEffect(() => onChangeValue(role, defaultValue), [defaultValue]);

    const onChangeText = (value) => {
        onChange && onChange(value);
        onChangeValue(role, value);
    };
    return (
        <ValidatedInput
            {...props}
            text={value[role]}
            onChange={onChangeText}
            validators={validators[role]}
            error={error[role] || defaultError}
        />
    );
};
